<template>
    <div class="wrapper">
        <div v-if="noProAV">
            <p style="text-align:center">未连接到设备</p>
        </div>
        <div v-else>
            <div id="matrix_contentDiv">
                <div id="matrix_content">
                    <p class="ProTitle">{{aoDataTitle}}</p>
                    <div class="table">
                        <div class="tableRow">
                            <div class="table_cell ">

                            </div>
                            <div class="table_cell">

                            </div>
                            <div class="table_cell1">

                            </div>
                            <div>
                                <div class="tableCell table_border" v-for="(items,index) in aoData.slice(a,b)" :key="index">
                                    <div class="groupHeader groupHeaderFirst">{{items.PHYName}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="tableRow">
                            <div class="table_cell">

                            </div>
                            <div class="table_cell">

                            </div>
                            <div class="table_cell1">
                                <div class="nav_left_content" :class="isleftHidden==true?'hide':''">
                                    <div class="nav_left_content_in" @click="turnLeft" :class="isleftHidden==true?'hide':''">

                                    </div>

                                </div>
                            </div>
                            <div>
                                <div class="tableCell table_border port_name" v-for="(items,index) in aoData.slice(c,d)" :key="index" :class="{selected:index==nowIndex1}" @mouseover="showInfo(index,items,'up')" @mouseleave="hideInfo()" @click="openSet(items,items.PhyID)">
                                    <div class="groupHeader groupHeaderSecound">{{items.Name}}</div>
                                </div>
                            </div>

                            <div class="table_cell">
                                <div class="nav_right" :class="isrightHidden==true?'hide':''">
                                    <div class="nav_right_content" @click="turnRight" :class="isrightHidden==true?'hide':''">

                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="tableRow1">
                            <div>
                            </div>
                            <div class="table_cell1">
                                <div class="nav_top" :class="istopHidden==true?'hide':''">
                                    <div class="nav_top_content_in" @click="turnTop" :class="istopHidden==true?'hide':''">

                                    </div>
                                </div>
                            </div>
                            <div>

                            </div>
                            <div>
                                <div v-for="(items,index) in aoData.slice(c,d)" :key="index">

                                </div>
                            </div>

                        </div>
                        <div class="tableRow" v-for="(items,index) in aoData.slice(e,f)" :key="index">
                            <div class="tableCell table_border">
                                <div class="groupRowFirst">{{items.PHYName}}</div>
                            </div>
                            <div class="tableCell table_border">
                                <div class="groupRow" :class="{selected:index==nowIndex}" @mouseover="showInfo(index,items)" @mouseleave="hideInfo()" @click="openSet(items,items.PhyID)">{{items.Name}}</div>
                            </div>
                            <div class="table_cell1">

                            </div>
                            <div v-if="showBtn">
                                <div class="table_cell" v-for="(interitems,interindex) in items.sourceGroup.slice(g,h)" :key="interindex">
                                    <div :class="{table_cell_bg:(index==bg_index&&interindex<=bg_index2)||interindex==bg_index2&&index<=bg_index}">
                                        <div v-if="interitems.link_status=='no'" class="link_status_no" @mouseenter="showPortLink(index,interindex)" @mouseleave="hidePortLink(index,interindex)">

                                        </div>
                                        <div v-else-if="interitems.link_status=='true'" class="link_status" :class="{'check':true}" @mouseenter="showPortLink(index,interindex)" @mouseleave="hidePortLink(index,interindex)">

                                        </div>
                                        <div v-else class="link_status" :class="{'check' : interitems.checked}" @click="selectedSwitch(interitems,items,items.PhyID,interitems.PhyID)" @mouseenter="showPortLink(index,interindex)" @mouseleave="hidePortLink(index,interindex)">

                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                        <div class="tableRow">
                            <div>

                            </div>
                            <div class="table_cell">
                                <div class="nav_bottom" :class="isdownHidden==true?'hide':''">
                                    <div class="nav_bottom_content" @click="turnDown" :class="isdownHidden==true?'hide':''">

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div :class="bottomInfo==false?'hide':''" class="bottom_info">
                        <p style="text-align:center;" v-show="bottomInfo">
                            <span class="infoName">Name: </span>
                            <span class="infoContent">{{bottom_name}}</span>
                            <span class="infoName">Channel: </span>
                            <span class="infoContent">{{Channel}}</span>
                            <span class="infoName" v-show="isSignal">Signal: </span>
                            <span class="infoContent" v-show="isSignal">{{bottom_signal}}</span>
                            <span class="infoName" v-show="isAudio">HDCP stat: </span>
                            <span class="infoContent" v-show="isAudio">{{HDCP_state}}</span>
                            <span class="infoName" v-show="isAudio">HDCP mode: </span>
                            <span class="infoContent" v-show="isAudio">{{hdcp_Mode}}</span>
                        </p>
                    </div>
                    <div v-if="openSetInfo" class="setInfo">
                        <div>
                            <button style="float:right" @click="closeSet">X</button>
                        </div>
                        <div v-show='isVideo'>
                            <p style="text-align:center;font-size:20px;">{{ChanelName}} Setting</p>
                            <p style="font-size:18px;margin-left:20px;">Port Information</p>
                            <table style="width:100%;margin-top:20px">
                                <tr>
                                    <td width=5%></td>
                                    <td width=40%>Type</td>
                                    <td width=35%>{{PortType}}</td>
                                    <td width=20%></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Name</td>
                                    <td><input type="text" v-model="Name" style="width:130px"></td>
                                    <td>

                                    </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Signal</td>
                                    <td>{{Signal}}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>HDCP Model</td>
                                    <td>
                                        <select name="" id="" v-model="HDCPModel">
                                            <option value="0">Support 1X</option>
                                            <option value="1">Support 2X</option>
                                            <option value="2">No Support</option>
                                        </select>
                                    </td>
                                    <td>

                                    </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>HDCP Status</td>
                                    <td>{{HDCPState}}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Mute</td>
                                    <td>
                                        <select name="" id="" v-model="Mute">
                                            <option value="0">On</option>
                                            <option value="1">Off</option>
                                        </select>
                                    </td>
                                    <td>

                                    </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>SignalStatus</td>
                                    <td>{{SignalStatus}}</td>
                                    <td>

                                    </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Resolution</td>
                                    <td>{{Resolution}}</td>
                                    <td>

                                    </td>
                                </tr>
                            </table>
                        </div>
                        <div v-show="!isVideo">
                            <p style="text-align:center;font-size:20px;">{{ChanelName}} Setting</p>
                            <p style="font-size:18px;margin-left:20px;">Port Information</p>
                            <table style="width:100%;margin-top:20px">
                                <tr>
                                    <td width=5%></td>
                                    <td width=30%>Name</td>
                                    <td width=65%><input type="text" v-model="audio_Name" style="width:130px"></td>

                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Port Type</td>
                                    <td>{{audio_Type}}</td>

                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Mute</td>
                                    <td>
                                        <select name="" id="" v-model="audio_Mute">
                                            <option value="0">On</option>
                                            <option value="1">Off</option>
                                        </select>
                                    </td>

                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Signal Status</td>
                                    <td>{{audio_SignalStatus}}</td>

                                </tr>
                                <tr>
                                    <td></td>
                                    <td>Sampling Rate</td>
                                    <td>{{audio_SamplingRate}}</td>

                                </tr>
                                <tr v-show="portType">
                                    <td></td>
                                    <td>3.5 Direction</td>
                                    <td>
                                        <select name="" id="" v-model="Direction" :disabled="DirAbility">
                                            <option value="0">In</option>
                                            <option value="1">Out</option>
                                        </select>
                                    </td>

                                </tr>
                                <tr v-show="portType">
                                    <td></td>
                                    <td>Volume</td>

                                    <td>
                                        <div class="block">
                                            <el-slider v-model="Volume" show-input>
                                            </el-slider>
                                        </div>
                                    </td>
                                </tr>
                                <tr v-show="portType">
                                    <td></td>
                                    <td>Balance</td>
                                    <td>
                                        <div class="block">
                                            <el-slider v-model="Balance" show-input>
                                            </el-slider>
                                        </div>
                                    </td>
                                </tr>
                            </table>
                        </div>
                        <div class="bottom_btn">
                            <button class="btn" @click="saveChange">OK</button>
                            <button class="btn" @click="closeSet">CANCEL</button>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
import bus from "../../utils/eventBus.js";
export default {
    components: {},
    props: {},
    data() {
        return {
            sourceGroup: "",
            key: "",
            name: "",
            proAvInfo: "",
            bottomInfo: false,
            noProAV: false,
            bottomInfo1: false,
            aoDataLength: "",
            aoData: "",
            aoDataTitle: "",
            nowIndex: "100",
            nowIndex1: "100",
            showBtn: true,
            isVideo: false,
            openSetInfo: false,
            isrightHidden: true,
            isleftHidden: true,
            istopHidden: true,
            isdownHidden: true,
            isdown: false,
            isright: false,
            a: 0,
            b: 16,
            c: 0,
            d: 16,
            e: 0,
            f: 16,
            g: 0,
            h: 16,
            bg_index: null,
            bg_index2: null,
            screenWidth: document.body.clientWidth,
            portType: false,
            SignalType: "",
            PhyID: "",
            hdcpMode: "",
            mute: "",
            direction: "",
            isAudio: false,
            moduleName: "",
            swtichInfo: [],
            swtichContent: []
        };
    },
    watch: {
        screenWidth(val) {
            if (!this.timer) {
                this.screenWidth = val;
                this.timer = true;
                let that = this;
                setTimeout(function() {
                    that.timer = false;
                }, 400);
            }
        },

        "$store.state.proAvInfo": function() {
            if (this.isdown == true) {
                this.isdownHidden = true;
            } else {
                this.isdownHidden = false;
            }
            if (this.isright == true) {
                this.isrightHidden = true;
            } else {
                this.isrightHidden = false;
            }
        },
        "$store.state.key": function() {
            this.key = this.$store.state.key;
            this.isrightHidden = true;
            this.isleftHidden = true;
            this.istopHidden = true;
            this.isdownHidden = true;
            this.openSetInfo = false;
            this.showBtn = true;
            this.a = 0;
            this.b = 16;
            this.c = 0;
            this.d = 16;
            this.e = 0;
            this.f = 16;
            this.g = 0;
            this.h = 16;
            this.isdown = false;
            this.isright = false;
        },
        "$store.state.moduleName": function() {
            this.moduleName = this.$store.state.moduleName;
        },
        "$store.state.swtichContent": function() {
            this.swtichContent = this.$store.state.swtichContent;
            this.swtichInfo = this.$store.state.swtichInfo;
            this.proAvInfo = this.$store.state.proAvInfo;
            let proAvInfo = this.proAvInfo.PortInfo;
            let channelNum = 0;
            let num = 1;
            let sourceGroup = [];
            for (let i = 0; i < proAvInfo.length; i++) {
                for (let j = 0; j < proAvInfo[i].ChannalInfo.length; j++) {
                    let ht = "";
                    ht = {
                        x_index: num,
                        y_index: num,
                        PhyIndex: proAvInfo[i].PhyIndex,
                        PortType: proAvInfo[i].PortType,
                        PhyID: proAvInfo[i].ChannalInfo[j].PhyID,
                        SignalType: proAvInfo[i].ChannalInfo[j].SignalType,
                        Name: proAvInfo[i].ChannalInfo[j].Name,
                        HDCPMode: proAvInfo[i].ChannalInfo[j].HDCPMode,
                        HDCPState: proAvInfo[i].ChannalInfo[j].HDCPState,
                        SignalStatus: proAvInfo[i].ChannalInfo[j].SignalStatus,
                        Signal: proAvInfo[i].ChannalInfo[j].Signal,
                        Resolution: proAvInfo[i].ChannalInfo[j].Resolution,
                        Connected: proAvInfo[i].ChannalInfo[j].Connected,
                        Mute: proAvInfo[i].ChannalInfo[j].Mute,
                        PHYName: proAvInfo[i].PHYName,
                        SamplingRate: proAvInfo[i].ChannalInfo[j].SamplingRate,
                        ARCAbility: proAvInfo[i].ChannalInfo[j].ARCAbility,
                        ARCStatus: proAvInfo[i].ChannalInfo[j].ARCStatus,
                        Volume: proAvInfo[i].ChannalInfo[j].Volume,
                        Balance: proAvInfo[i].ChannalInfo[j].Balance,
                        DirAbility: proAvInfo[i].ChannalInfo[j].DirAbility,
                        Dir: proAvInfo[i].ChannalInfo[j].Dir,
                        link_status: "false"
                    };
                    sourceGroup.push(ht);
                    num = num + 1;
                }
                channelNum += Number(proAvInfo[i].ChannalInfo.length);
            }
            let channelNum1 = 0;
            let num1 = 1;
            let sourceGroup1 = [];

            for (let i = 0; i < proAvInfo.length; i++) {
                for (let j = 0; j < proAvInfo[i].ChannalInfo.length; j++) {
                    let ht = "";
                    ht = {
                        x_index: num1,
                        PhyIndex: proAvInfo[i].PhyIndex,
                        PortType: proAvInfo[i].PortType,
                        PhyID: proAvInfo[i].ChannalInfo[j].PhyID,
                        SignalType: proAvInfo[i].ChannalInfo[j].SignalType,
                        Name: proAvInfo[i].ChannalInfo[j].Name,
                        HDCPMode: proAvInfo[i].ChannalInfo[j].HDCPMode,
                        HDCPState: proAvInfo[i].ChannalInfo[j].HDCPState,
                        SignalStatus: proAvInfo[i].ChannalInfo[j].SignalStatus,
                        Signal: proAvInfo[i].ChannalInfo[j].Signal,
                        Resolution: proAvInfo[i].ChannalInfo[j].Resolution,
                        Connected: proAvInfo[i].ChannalInfo[j].Connected,
                        Mute: proAvInfo[i].ChannalInfo[j].Mute,
                        PHYName: proAvInfo[i].PHYName,
                        SamplingRate: proAvInfo[i].ChannalInfo[j].SamplingRate,
                        ARCAbility: proAvInfo[i].ChannalInfo[j].ARCAbility,
                        ARCStatus: proAvInfo[i].ChannalInfo[j].ARCStatus,
                        Volume: proAvInfo[i].ChannalInfo[j].Volume,
                        Balance: proAvInfo[i].ChannalInfo[j].Balance,
                        DirAbility: proAvInfo[i].ChannalInfo[j].DirAbility,
                        Dir: proAvInfo[i].ChannalInfo[j].Dir,
                        sourceGroup: JSON.parse(JSON.stringify(sourceGroup))
                    };
                    sourceGroup1.push(ht);
                    num1 = num1 + 1;
                }
                channelNum1 += Number(proAvInfo[i].ChannalInfo.length);
            }
            // console.log(sourceGroup1);

            this.aoData = sourceGroup1;
            this.aoDataLength = sourceGroup1.length;
            this.aoDataTitle =
                this.proAvInfo.BaseInfo.Name +
                " " +
                this.proAvInfo.BaseInfo.ModuleName;
            for (let k = 0; k < this.swtichContent.length; k++) {
                var macrosSwitchInfo = this.swtichContent[k].split(">");
                console.log(macrosSwitchInfo);
                for (let i = 0; i < this.aoData.length; i++) {
                    if (
                        this.aoData[i].PhyID == JSON.parse(macrosSwitchInfo[0])
                    ) {
                        console.log(this.aoData[i]);
                        let sourceGroupArray = [];
                        sourceGroupArray = this.aoData[i].sourceGroup;
                        for (let x = 0; x < sourceGroupArray.length; x++) {
                            if (
                                sourceGroupArray[x].PhyID == macrosSwitchInfo[1]
                            ) {
                                sourceGroupArray[x].link_status = "true";
                            }
                        }
                    }
                }
            }
            for (let i = 0; i < this.aoData.length; i++) {
                for (let k = 0; k < this.aoData[i].sourceGroup.length; k++) {
                    if (
                        this.aoData[i].PhyID ==
                        this.aoData[i].sourceGroup[k].PhyID
                    ) {
                        this.aoData[i].sourceGroup[k].link_status = "no";
                    }
                }
            }
            console.log(this.swtichContent);
        }
    },
    computed: {},
    methods: {
        selectedSwitch(item, items, c, d) {
            let switchContentInfo = "";
            switchContentInfo =
                items.PHYName +
                "_" +
                items.Name +
                "<-->" +
                item.PHYName +
                "_" +
                item.Name;
            console.log(switchContentInfo);
            let switchBtn = c + ">" + d;

            // if (item.link_status == "false") {
            //     item.link_statu = "true";
            // }
            if (!item.checked) {
                this.$set(item, "checked", true);
                this.$store.state.switchContentInfo = switchContentInfo;
                this.$store.state.switchBtn = switchBtn;
            }
        },
        showInfo(index, item, isPosition) {
            if (this.screenWidth > 1025) {
                if (isPosition == "up") {
                    this.nowIndex1 = index;
                } else {
                    this.nowIndex = index;
                }
                if (item.SignalType == "Video") {
                    this.isAudio = true;
                    this.HDCP_state = item.HDCPState;
                    this.hdcp_Mode = item.HDCPMode;
                } else if (item.SignalType == "Audio") {
                    this.isAudio = false;
                }
                this.bottom_name = item.Name;
                this.Channel = item.PhyIndex;
                if (item.Signal == undefined || item.Signal == "") {
                    this.isSignal = false;
                } else {
                    this.isSignal = true;
                    this.bottom_signal = item.Signal;
                }
                this.bottomInfo = true;
            }
        },
        hideInfo(index) {
            if (this.screenWidth > 1025) {
                this.bottomInfo = false;
                this.nowIndex = "100";
                this.nowIndex1 = "100";
            }
        },
        openSet(items, phyID) {
            this.openSetInfo = true;
            this.ChanelName = items.Name;
            this.SignalType = items.SignalType;
            this.PhyID = phyID;
            // console.log(items);
            if (items.SignalType == "Video") {
                this.isVideo = true;
                this.showBtn = false;
                this.PortType = items.PortType;
                this.Name = items.Name;
                this.Signal = items.Signal;
                this.HDCPState = items.HDCPState;
                this.Resolution = items.Resolution;
                this.SignalStatus = items.SignalStatus;
                if (items.HDCPMode == "1.x") {
                    this.HDCPModel = 0;
                } else if (items.HDCPMode == "2.x") {
                    this.HDCPModel = 1;
                } else if (items.HDCPMode == "no") {
                    this.HDCPModel = 2;
                }
                if (items.Mute == "On" || items.Mute == 0) {
                    this.Mute = 0;
                } else if (items.Mute == "Off" || items.Mute == "1") {
                    this.Mute = 1;
                }
            } else if (items.SignalType == "Audio") {
                this.isVideo = false;
                this.showBtn = false;
                this.audio_Name = items.Name;
                this.audio_Type = items.PortType;
                this.audio_SignalStatus = items.SignalStatus;
                this.audio_SamplingRate = items.SamplingRate;

                if (items.PortType == "3.5") {
                    this.portType = true;
                    if (items.DirAbility == false) {
                        this.DirAbility = true;
                    } else {
                        this.DirAbility = false;
                    }
                    if (items.Dir == "in") {
                        this.Direction = 0;
                    } else if (items.Dir == "out") {
                        this.Direction = 1;
                    }
                    this.Volume = items.Volume;
                    this.Balance = items.Balance;
                } else {
                    this.portType = false;
                }

                if (items.Mute == "On" || items.Mute == 0) {
                    this.audio_Mute = 0;
                } else if (items.Mute == "Off" || items.Mute == "1") {
                    this.audio_Mute = 1;
                }
            }
        },
        closeSet() {
            this.showBtn = true;
            this.openSetInfo = false;
        },
        saveChange() {
            if (this.SignalType == "Video") {
                if (this.HDCPModel == 0) {
                    this.hdcpMode = "1.x";
                } else if (this.HDCPModel == 1) {
                    this.hdcpMode = "2.x";
                } else if (this.HDCPModel == 2) {
                    this.hdcpMode = "no";
                }
                if (this.Mute == 0) {
                    this.mute = "On";
                } else if (this.Mute == 1) {
                    this.mute = "Off";
                }
                var timestamp = new Date().valueOf();
                let username = sessionStorage.getItem("access_username");
                let sessionId = sessionStorage.getItem("access_token");
                let aoData = {
                    Authentication: {
                        UserName: username,
                        SessionId: sessionId
                    },
                    data: [
                        {
                            CommandType: "set",
                            CommandData: {
                                CommandName: "ChannalInfo",
                                key: this.key,
                                PhyID: this.PhyID,
                                ChannalInfo: {
                                    Name: this.Name,
                                    HDCPMode: this.hdcpMode,
                                    Mute: this.mute
                                }
                            }
                        }
                    ],
                    Protocol: {
                        Name: "liguoNetWork",
                        Version: "1.0.0"
                    },
                    JSON: {
                        Version: "1.0.0"
                    },
                    datalen: 1,
                    request: timestamp,
                    timestamp: timestamp
                };
                let that = this;
                this.$axios
                    .post("/cmd", aoData)
                    .then(function(response) {
                        if (response.data.status == "success") {
                            alert("设置成功");
                            that.openSetInfo = false;
                            that.showBtn = true;
                            that.getProInfo();
                        } else if (response.data.status == "fail") {
                            alert(response.data.error.errorstring.cn);
                        }
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else if (this.SignalType == "Audio") {
                var timestamp = new Date().valueOf();
                let username = sessionStorage.getItem("access_username");
                let sessionId = sessionStorage.getItem("access_token");
                let aoData = "";
                if (this.audio_Mute == 0) {
                    this.mute = "On";
                } else if (this.audio_Mute == 1) {
                    this.mute = "Off";
                }
                if (this.audio_Type == "3.5") {
                    if (this.Direction == 0) {
                        this.direction = "in";
                    } else if (this.Direction == 1) {
                        this.direction = "out";
                    }
                    aoData = {
                        Authentication: {
                            UserName: username,
                            SessionId: sessionId
                        },
                        data: [
                            {
                                CommandType: "set",
                                CommandData: {
                                    CommandName: "ChannalInfo",
                                    key: this.key,
                                    PhyID: this.PhyID,
                                    ChannalInfo: {
                                        Name: this.audio_Name,
                                        Mute: this.audio_Mute,
                                        Dir: this.direction,
                                        Volume: this.Volume,
                                        Balance: this.Balance
                                    }
                                }
                            }
                        ],
                        Protocol: {
                            Name: "liguoNetWork",
                            Version: "1.0.0"
                        },
                        JSON: {
                            Version: "1.0.0"
                        },
                        datalen: 1,
                        request: timestamp,
                        timestamp: timestamp
                    };
                } else {
                    aoData = {
                        Authentication: {
                            UserName: username,
                            SessionId: sessionId
                        },
                        data: [
                            {
                                CommandType: "set",
                                CommandData: {
                                    CommandName: "ChannalInfo",
                                    key: this.key,
                                    PhyID: this.PhyID,
                                    ChannalInfo: {
                                        Name: this.audio_Name,
                                        Mute: this.mute
                                    }
                                }
                            }
                        ],
                        Protocol: {
                            Name: "liguoNetWork",
                            Version: "1.0.0"
                        },
                        JSON: {
                            Version: "1.0.0"
                        },
                        datalen: 1,
                        request: timestamp,
                        timestamp: timestamp
                    };
                }

                let that = this;
                this.$axios
                    .post("/cmd", aoData)
                    .then(function(response) {
                        if (response.data.status == "success") {
                            alert("设置成功");
                            that.openSetInfo = false;
                            that.showBtn = true;
                            that.getProInfo();
                        } else if (response.data.status == "fail") {
                            alert(response.data.error.errorstring.cn);
                        }
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            }
        },
        turnRight() {
            if (this.b < this.aoDataLength) {
                if (this.b == this.aoDataLength - 2) {
                    this.isrightHidden = true;
                    this.isright = true;
                }
                this.a += 2;
                this.b += 2;
                this.c += 2;
                this.d += 2;
                this.g += 2;
                this.h += 2;
                this.isleftHidden = false;
            }
        },
        turnLeft() {
            if (this.a > 0) {
                if (this.a == 2) {
                    this.isleftHidden = true;
                }
                this.a -= 2;
                this.b -= 2;
                this.c -= 2;
                this.d -= 2;
                this.g -= 2;
                this.h -= 2;
                this.isrightHidden = false;
                this.isright = false;
            }
        },
        turnTop() {
            if (this.e > 0) {
                if (this.e == 2) {
                    this.istopHidden = true;
                }
                this.e -= 2;
                this.f -= 2;
                this.isdownHidden = false;
                this.isdown = false;
            }
        },
        turnDown() {
            if (this.f < this.aoDataLength) {
                if (this.f == this.aoDataLength - 2) {
                    this.isdownHidden = true;
                    this.isdown = true;
                }
                this.e += 2;
                this.f += 2;
                this.istopHidden = false;
            }
        },
        showPortLink(a, b) {
            this.bg_index = a;
            this.bg_index2 = b;
            // console.log(a + "," + b);
        },
        hidePortLink() {
            this.bg_index = null;
            this.bg_index2 = null;
        },
        getProInfo() {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "get",
                        CommandData: {
                            CommandName: "DeviceInfo",
                            key: this.key,
                            ModuleName: this.moduleName
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    let proAvInfo = response.data.data.PortInfo;
                    let channelNum = 0;
                    let num = 1;
                    let sourceGroup = [];
                    for (let i = 0; i < proAvInfo.length; i++) {
                        for (
                            let j = 0;
                            j < proAvInfo[i].ChannalInfo.length;
                            j++
                        ) {
                            let ht = "";
                            ht = {
                                x_index: num,
                                y_index: num,
                                PhyIndex: proAvInfo[i].PhyIndex,
                                PortType: proAvInfo[i].PortType,
                                PhyID: proAvInfo[i].ChannalInfo[j].PhyID,
                                SignalType:
                                    proAvInfo[i].ChannalInfo[j].SignalType,
                                Name: proAvInfo[i].ChannalInfo[j].Name,
                                HDCPMode: proAvInfo[i].ChannalInfo[j].HDCPMode,
                                HDCPState:
                                    proAvInfo[i].ChannalInfo[j].HDCPState,
                                SignalStatus:
                                    proAvInfo[i].ChannalInfo[j].SignalStatus,
                                Signal: proAvInfo[i].ChannalInfo[j].Signal,
                                Resolution:
                                    proAvInfo[i].ChannalInfo[j].Resolution,
                                Connected:
                                    proAvInfo[i].ChannalInfo[j].Connected,
                                Mute: proAvInfo[i].ChannalInfo[j].Mute,
                                PHYName: proAvInfo[i].PHYName,
                                SamplingRate:
                                    proAvInfo[i].ChannalInfo[j].SamplingRate,
                                ARCAbility:
                                    proAvInfo[i].ChannalInfo[j].ARCAbility,
                                ARCStatus:
                                    proAvInfo[i].ChannalInfo[j].ARCStatus,
                                Volume: proAvInfo[i].ChannalInfo[j].Volume,
                                Balance: proAvInfo[i].ChannalInfo[j].Balance,
                                DirAbility:
                                    proAvInfo[i].ChannalInfo[j].DirAbility,
                                Dir: proAvInfo[i].ChannalInfo[j].Dir,
                                link_status: "false"
                            };
                            sourceGroup.push(ht);
                            num = num + 1;
                        }
                        channelNum += Number(proAvInfo[i].ChannalInfo.length);
                    }
                    let channelNum1 = 0;
                    let num1 = 1;
                    let sourceGroup1 = [];

                    for (let i = 0; i < proAvInfo.length; i++) {
                        for (
                            let j = 0;
                            j < proAvInfo[i].ChannalInfo.length;
                            j++
                        ) {
                            let ht = "";
                            ht = {
                                x_index: num1,
                                PhyIndex: proAvInfo[i].PhyIndex,
                                PortType: proAvInfo[i].PortType,
                                PhyID: proAvInfo[i].ChannalInfo[j].PhyID,
                                SignalType:
                                    proAvInfo[i].ChannalInfo[j].SignalType,
                                Name: proAvInfo[i].ChannalInfo[j].Name,
                                HDCPMode: proAvInfo[i].ChannalInfo[j].HDCPMode,
                                HDCPState:
                                    proAvInfo[i].ChannalInfo[j].HDCPState,
                                SignalStatus:
                                    proAvInfo[i].ChannalInfo[j].SignalStatus,
                                Signal: proAvInfo[i].ChannalInfo[j].Signal,
                                Resolution:
                                    proAvInfo[i].ChannalInfo[j].Resolution,
                                Connected:
                                    proAvInfo[i].ChannalInfo[j].Connected,
                                Mute: proAvInfo[i].ChannalInfo[j].Mute,
                                PHYName: proAvInfo[i].PHYName,
                                SamplingRate:
                                    proAvInfo[i].ChannalInfo[j].SamplingRate,
                                ARCAbility:
                                    proAvInfo[i].ChannalInfo[j].ARCAbility,
                                ARCStatus:
                                    proAvInfo[i].ChannalInfo[j].ARCStatus,
                                Volume: proAvInfo[i].ChannalInfo[j].Volume,
                                Balance: proAvInfo[i].ChannalInfo[j].Balance,
                                DirAbility:
                                    proAvInfo[i].ChannalInfo[j].DirAbility,
                                Dir: proAvInfo[i].ChannalInfo[j].Dir,
                                sourceGroup: JSON.parse(
                                    JSON.stringify(sourceGroup)
                                )
                            };
                            sourceGroup1.push(ht);
                            num1 = num1 + 1;
                        }
                        channelNum1 += Number(proAvInfo[i].ChannalInfo.length);
                    }
                    // console.log(sourceGroup1);

                    that.aoData = sourceGroup1;
                    that.aoDataLength = sourceGroup1.length;
                    that.aoDataTitle =
                        that.proAvInfo.BaseInfo.Name +
                        " " +
                        that.proAvInfo.BaseInfo.ModuleName;
                    that.moduleName = that.proAvInfo.BaseInfo.ModuleName;
                    that.aoData = sourceGroup1;
                    for (let i = 0; i < that.aoData.length; i++) {
                        for (
                            let k = 0;
                            k < that.aoData[i].sourceGroup.length;
                            k++
                        ) {
                            if (
                                that.aoData[i].PhyID ==
                                that.aoData[i].sourceGroup[k].PhyID
                            ) {
                                that.aoData[i].sourceGroup[k].link_status =
                                    "no";
                            }
                        }
                        // if (that.aoData[i].Dir == "in") {
                        //     let connected = [];
                        //     connected = that.aoData[i].Connected;
                        //     let sourceGroupArray = [];
                        //     sourceGroupArray = that.aoData[i].sourceGroup;

                        //     if (connected.length > 0) {
                        //         for (let j = 0; j < connected.length; j++) {
                        //             for (
                        //                 let x = 0;
                        //                 x < sourceGroupArray.length;
                        //                 x++
                        //             ) {
                        //                 if (
                        //                     sourceGroupArray[x].PhyID ==
                        //                     connected[j]
                        //                 ) {
                        //                     sourceGroupArray[x].link_status =
                        //                         "true";
                        //                 }
                        //             }
                        //         }
                        //     }
                        // }
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    },
    created() {},
    mounted() {
        this.key = this.$store.state.key;
        const that = this;
        window.onresize = () => {
            return (() => {
                window.screenWidth = document.body.clientWidth;
                that.screenWidth = window.screenWidth;
            })();
        };
    }
};
</script>
<style lang="scss" scoped>
@import "../../style/common";
.wrapper {
    width: 100%;
}
.hide {
    visibility: hidden;
}
#matrix_contentDiv {
    position: relative;
    padding: 0px 10px;
}
#matrix_content {
    width: 790px;
    height: 780px;
    min-height: 780px;
    margin: 0 auto;
    position: relative;
}
.ProTitle {
    text-align: center;
    font-size: 20px;
    margin-bottom: 10px;
}
.table {
    display: table;
    border-collapse: collapse;
    table-layout: fixed;
}
.tableRow {
    display: table-row;
    height: 25px;
}
.tableRow1 {
    display: table-row;
    height: 10px;
}
.tableCell,
.table_cell {
    width: 30px;
    height: 30px;
    display: table-cell;
    vertical-align: middle;
    box-sizing: border-box;
}
.table_cell1 {
    width: 10px;
    display: table-cell;
    position: relative;
}
.tableCell {
    background-color: white;
}
.selected {
    background-color: #ccc;
}
.groupHeader {
    text-align: center;
    transform: translate(0, 100%) rotate(-90deg);
    transform-origin: 0 0;
    white-space: nowrap;
    // width: 20px;
    margin-bottom: 5px;
    line-height: 30px;
}

.groupHeaderFirst {
    width: 29px;
    height: 75px;
    box-sizing: border-box;
}
.groupHeaderSecound {
    width: 29px;
    height: 113px;
    box-sizing: border-box;
}
.table_border {
    border: 1px solid #4d4d4d;
}
.groupRow {
    width: 120px;
    cursor: pointer;
    height: 30px;
    box-sizing: border-box;
    padding-right: 5px;
    text-align: right;
    line-height: 30px;
}
.groupRowFirst {
    width: 75px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (min-width: 1025px) {
    .link_status,
    .link_status_no {
        width: 29px;
        height: 29px;
        cursor: pointer;
        margin: auto;
        display: block;
        // box-sizing: border-box;
        background: url(../../../static/img/yuan2.png) no-repeat;
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        border-radius: 15px;
        z-index: 99;
    }
    .link_status_no {
        background: url(../../../static/img/yuan3.png) no-repeat;
        cursor: no-drop;
    }
    .check {
        // background-color: #009a61;
        background: url(../../../static/img/sprite.png) no-repeat -120px -149px;
    }
}

@media (max-width: 1025px) {
    .link_status,
    .link_status_no {
        width: 29px;
        height: 29px;
        cursor: pointer;
        margin: auto;
        display: block;
        box-sizing: border-box;
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        border-radius: 15px;
        box-shadow: 1px 1px 1px 0 #777;
        -moz-box-shadow: 1px 1px 1px 0 #777;
        -webkit-box-shadow: 1px 1px 1px 0 #777;
        -o-box-shadow: 1px 1px 1px 0 #777;
        background-color: white;
        z-index: 99;
    }
    .link_status_no {
        background-color: #ccc;
        cursor: no-drop;
    }
    .check {
        background: url(../../../static/img/sprite.png) no-repeat -120px -149px;
    }
}

.nav_left_content {
    display: block;
    width: 0;
    height: 0;
    cursor: pointer;
    border-top: 60px solid transparent;
    border-right: 30px solid #4d4d4d;
    border-bottom: 60px solid transparent;
    position: absolute;
    top: 0;
    left: -19px;
}
.nav_left_content_in {
    display: block;
    width: 0;
    height: 0;
    cursor: pointer;
    border-top: 56px solid transparent;
    border-right: 27px solid white;
    border-bottom: 56px solid transparent;
    position: absolute;
    top: -56px;
    left: 2px;
}
.nav_right {
    width: 0;
    height: 0;
    border-top: 60px solid transparent;
    border-left: 30px solid #4d4d4d;
    border-bottom: 60px solid transparent;
    position: relative;
}
.nav_right_content {
    display: block;
    width: 0;
    height: 0;
    cursor: pointer;
    border-top: 56px solid transparent;
    border-left: 27px solid white;
    border-bottom: 56px solid transparent;
    position: absolute;
    top: -56px;
    left: -29px;
}
.nav_top {
    width: 0;
    height: 0;
    border-left: 60px solid transparent;
    border-right: 60px solid transparent;
    border-bottom: 30px solid #4d4d4d;
    position: absolute;
    top: -20px;
    left: 0px;
}
.nav_top_content_in {
    display: block;
    width: 0;
    height: 0;
    cursor: pointer;
    border-left: 56px solid transparent;
    border-right: 56px solid transparent;
    border-bottom: 27px solid white;
    position: absolute;
    top: 2px;
    left: -56px;
}
.nav_bottom {
    width: 0;
    height: 0;
    border-left: 60px solid transparent;
    border-right: 60px solid transparent;
    border-top: 30px solid #4d4d4d;
    position: relative;
}
.nav_bottom_content {
    display: block;
    cursor: pointer;
    width: 0;
    height: 0;
    border-left: 56px solid transparent;
    border-right: 56px solid transparent;
    border-top: 27px solid white;
    position: absolute;
    top: -29px;
    left: -56px;
}
.nav_bottom_content:hover {
    border-top: 28px solid #ededed;
}
.nav_left_content_in:hover {
    border-right: 27px solid #ededed;
}
.nav_top_content_in:hover {
    border-bottom: 27px solid #ededed;
}
.nav_right_content:hover {
    border-left: 28px solid #ededed;
}

.port_name {
    cursor: pointer;
}
.infoContent {
    margin-right: 15px;
}
.bottom_info {
    width: 670px;
    height: 25px;
    border: 1px solid #ccc;
    margin-top: 5px;
}
.setInfo {
    width: 480px;
    height: 490px;
    border: 1px solid #ccc;
    position: absolute;
    top: 250px;
    left: 209px;
    background-color: white;
    z-index: 100;
}
.bottom_btn {
    text-align: center;
    width: 475px;
    position: absolute;
    bottom: 20px;
}
.btn {
    width: 100px;
    height: 30px;
}
.btn:first-child {
    margin-right: 50px;
}
button {
    cursor: pointer;
}
.table_cell_bg {
    background-color: #4a7bb3;
}
</style>


