<template>
    <div id="content">
        <section class="deviecList">
            <Devicelist @setting="settingInfo"></Devicelist>
        </section>
        <section v-show="switchInfo&&situational" v-loading="loading" element-loading-background="white" class="switchView">
            <Switchview @loadingStatus="loadingInfo"></Switchview>
        </section>
        <section v-show="switchInfo&&!situational" v-loading="loading" element-loading-background="white" class="switchView">
            <SwitchMode @loadingStatus="loadingInfo"></SwitchMode>
        </section>
        <section v-show="switchInfo" class="situationalMode" v-loading="loading" element-loading-background="white" :style="aside_nav[selectIndex]">
            <Situationalmode @nav_switch="changeNavInfo"></Situationalmode>
        </section>
        <section v-show="!switchInfo" class="settingInfo">
            <SettingInfo @closeSetting="closeInfo"></SettingInfo>
        </section>
    </div>
</template>

<script>
import Devicelist from "./switchView/deviceList";
import Switchview from "./switchView/switchView";
import Situationalmode from "./switchView/situationalMode";
import SettingInfo from "./switchView/SettingInfo";
import SwitchMode from "./switchView/SwitchMode";
export default {
    name: "SwtichContent",
    data() {
        return {
            aside_nav: [
                "min-width: 214px;width:214px",
                "min-width: 444px;width:444px",
                "min-width: 0px;width: 0px"
            ],
            selectIndex: 0,
            loading: false,
            switchInfo: true,
            situational: true
        };
    },
    components: {
        Devicelist,
        Switchview,
        Situationalmode,
        SettingInfo,
        SwitchMode
    },
     watch: {
        "$store.state.key": function() {
          this.situational = true;
        }
    },
    methods: {
        changeNavInfo: function(data) {
            if (data == "close_nav") {
                this.selectIndex = 0;
                this.situational = true;
            } else if (data == "hide_nav") {
                this.selectIndex = 2;
            } else if (data == "open_nav") {
                this.selectIndex = 1;
                this.situational = false;
            }
        },
        loadingInfo: function(data) {
            if (data == "endLoading") {
                this.loading = false;
            } else if (data == "startLoading") {
                this.loading = true;
            }
        },
        settingInfo: function(data) {
            if (data == "settingInfo") {
                this.switchInfo = false;
            } else if (data == "startLoading") {
                this.loading = true;
            }
        },
        closeInfo: function(data) {
            if (data == "close") {
                this.switchInfo = true;
            } else if (data == "startLoading") {
                this.loading = true;
            }
        }
    }
};
</script>

<style scoped lang="scss">
@import "../style/common";
#content {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    padding: 10px;
    height: 100%;
}
.deviecList {
    margin-right: 10px;
    width: 200px;
    min-width: 200px;
    height: calc(100% - 20px);
}
.switchView {
    width: auto;
    height: calc(100% - 20px);
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}
.situationalMode {
    height: calc(100% - 20px);
}
.settingInfo {
    width: 100%;
    background: white;

    height: calc(100% - 20px);
    overflow: auto;
    margin-left: 10px;
    box-shadow: 2px 2px 2px #ccc;
}
</style>



