<template>
    <div class="wrapper">
        <!-- 左侧 -->
        <transition name="slide-fade">
            <div class="wrapper_left" v-show="displayMacro">
                <!-- 关闭按钮 -->
                <div class="hiddenDutton" @click="hiddenButton"></div>
                <!-- 上面头部位置 -->
                <div class="wrapper_left_topBox">
                    <p class="wrapper_left_plot">Scenarios</p>
                    <p class="wrapper_left_preservation">Save current view snapshot</p>
                    <!-- 保存宏 -->
                    <input class="wrapper_left_keepName" type="text" placeholder="Name" ref="name_inp" value="" maxlength="12" onkeyup="value=value.replace(/[^\a-\z\A-\Z0-9_]/g,'')">
                    <button class="wrapper_left_button" :disabled="displayMacro1==true?true:false" :class="displayMacro1==true?'wrapper_left_nopreservationBton':' wrapper_left_preservationBton'" @click="goQuery"></button>
                    <!-- 搜索宏 -->
                    <div class="clear wrapper_left_searchMacro">
                        <input class="wrapper_left_searchMacro_inpputMacro" maxlength="12" type="text" placeholder="search" v-model="searchVal">
                    </div>
                </div>
                <!-- 中间分离线-->
                <div style="height: 10px;background: #E4E4E4;margin-top: 5px;"></div>
                <!-- 下面创建的宏 -->
                <div class="wrapper_left_bottomBox">
                    <div class="wrapper_left_bottomBox_block" v-for="(items,index) in bottomBox" :key="index">
                        <p ref="macroName" style="font-size: 14px;margin-top: 5px;">{{items.Name}}</p>
                        <div class="wrapper_left_bottomBox_block_ico"></div>
                        <div class="wrapper_left_bottomBox_block_ico_box">
                            <!-- 显示按钮 -->
                            <div class="wrapper_left_bottomBox_block_btn block_btn1" @click="switchMacros(index,items.Switch,items.SwitchInfo,items.Name),show = !show"></div>
                            <!-- 运行按钮 -->
                            <div class="wrapper_left_bottomBox_block_btn block_btn2" @click="implementMacro(index,items.Name)"></div>
                            <!-- 删除按钮 -->
                            <div class="wrapper_left_bottomBox_block_btn block_btn3" @click="deleteAtBottom(index,items.Name)"></div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- 右侧-->
        <transition name="slide-fade">
            <div class="wrapper_right" v-show="displayMacro1">
                <div class="wrapper_right_head">
                    <!-- 缩小按钮 -->
                    <div class="wrapper_right_head_btn right_head_btn_close" @click="closeButton"></div>
                    <!-- 清空按钮 -->
                    <div class="wrapper_right_head_btn right_head_btn_empty" @click="emptyingRecord"></div>
                    <p style="font-size: 14px;">Scenario<br>recording</p>
                    <input class="wrapper_right_head_input" type="text" placeholder="Name" ref="nameInpt" value="" maxlength="12" onkeyup="value=value.replace(/[^\a-\z\A-\Z0-9_]/g,'')">
                    <button class="wrapper_left_button wrapper_right_head_keepBtn" style="margin-left: 10px;" @click="reviseBtn"></button>
                </div>
                <!-- 下面的每一步宏操作 -->
                <div class="wrapper_right_bottom">
                    <div class="wrapper_right_bottom_box clear" v-for="(items,index) in rightBottom" :key="index">
                        <span class="wrapper_right_bottom_box_number">{{index+1}}</span>
                        <div class="wrapper_right_bottom_box_right">
                            <p class="wrapper_right_bottom_box_name" :title="items">{{items}}</p>
                            <!-- <p class="wrapper_right_bottom_box_record">{{items.boxRecord[0]}},{{items.boxRecord[1]}}</p> -->
                        </div>
                        <div class="wrapper_right_bottom_box_delete" @click="deleteRightBottom(index)"></div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- 显示按钮 -->
        <div v-show="btn_hide" class="classDisplayMacro" @click="hiddenButton"></div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            bottomBoxBlock: [],
            rightBottom: [],
            quanju: new Array(),
            bubian: new Array(),
            testindex: 0,
            hidden: true,
            display: false,
            searchVal: "",
            show: true,
            displayMacro: true,
            displayMacro1: false,
            btn_hide: false,
            hongname: "",
            proAvInfo: "",
            key: "",
            selected: [],
            MacrosSwitchInfo: [],
            quanjuMacrosSwitchInfo: new Array(),
            bubianMacrosSwitchInfo: new Array()
        };
    },
    watch: {
        "$store.state.switchContentInfo": function() {
            let that = this;
            let switchContentInfo = that.$store.state.switchContentInfo;
            console.log(switchContentInfo);

            this.rightBottom.push(switchContentInfo);
            this.quanju.push(switchContentInfo);
            console.log(this.rightBottom);
        },
        "$store.state.switchBtn": function() {
            let that = this;
            let switchData = that.$store.state.switchBtn;
            this.MacrosSwitchInfo.push(switchData);
            this.quanjuMacrosSwitchInfo.push(switchData);
            console.log(this.MacrosSwitchInfo);
        },
        "$store.state.proAvInfo": function() {
            this.proAvInfo = this.$store.state.proAvInfo;
            this.bottomBoxBlock = this.proAvInfo.Macro;
        },
        "$store.state.key": function() {
            this.key = this.$store.state.key;
            this.hidden = true;
            this.display = false;
            this.show = true;
            this.displayMacro = true;
            this.displayMacro1 = false;
            this.$emit("nav_switch", "close_nav");
        },
        "$store.state.selected": function() {
            this.selected = this.$store.state.selected;
        }
    },
    computed: {
        // 搜索宏
        bottomBox: function() {
            var searchVal = this.searchVal;
            if (searchVal) {
                return this.bottomBoxBlock.filter(function(bottomBoxBlock) {
                    return Object.keys(bottomBoxBlock).some(function(key) {
                        return (
                            String(bottomBoxBlock[key]).indexOf(searchVal) > -1
                        );
                    });
                });
            }
            return this.bottomBoxBlock;
        }
    },
    methods: {
        // 显示隐藏宏按钮
        hiddenButton: function() {
            this.displayMacro = !this.displayMacro;
            if (this.displayMacro) {
                this.btn_hide = false;
                console.log("展开");
                if (this.displayMacro1 == false) {
                    this.$emit("nav_switch", "close_nav");
                } else {
                    this.$emit("nav_switch", "open_nav");
                }
            } else {
                this.btn_hide = true;
                console.log("隐藏");

                this.$emit("nav_switch", "hide_nav");
            }
        },
        /* 添加宏
            1.宏没有输入名称不可保存
            2.输入宏名称有重复不可保存
        */
        goQuery: function() {
            if (this.$refs.name_inp.value == "") {
                this.$alert("名字不可为空", "提示", {
                    confirmButtonText: "确定",
                    callback: action => {}
                });
            } else {
                var list_bj = true;
                for (let i = 0; i < this.bottomBoxBlock.length; i++) {
                    if (
                        this.bottomBoxBlock[i].Name == this.$refs.name_inp.value
                    ) {
                        this.$alert("名字不可重复", "提示", {
                            confirmButtonText: "确定",
                            callback: action => {}
                        });
                        list_bj = false;
                        return;
                    }
                }
                if (list_bj) {
                    let selectedData = this.$store.state.selected;
                    var timestamp = new Date().valueOf();
                    let username = sessionStorage.getItem("access_username");
                    let sessionId = sessionStorage.getItem("access_token");
                    let aoData = {
                        Authentication: {
                            UserName: username,
                            SessionId: sessionId
                        },
                        data: [
                            {
                                CommandType: "set",
                                CommandData: {
                                    CommandName: "AddMacro",
                                    key: this.key,
                                    macro: {
                                        name: this.$refs.name_inp.value,
                                        data: selectedData
                                    }
                                }
                            }
                        ],
                        Protocol: {
                            Name: "liguoNetWork",
                            Version: "1.0.0"
                        },
                        JSON: {
                            Version: "1.0.0"
                        },
                        datalen: 1,
                        request: timestamp,
                        timestamp: timestamp
                    };
                    let that = this;
                    this.$axios
                        .post("/cmd", aoData)
                        .then(function(response) {
                            if (response.data.status == "success") {
                                alert("保存成功");
                                that.$refs.name_inp.value = "";
                                that.display = !that.hidden;
                                that.displayMacro1 = false;
                                that.$emit("nav_switch", "close_nav");
                            } else if (response.data.status == "fail") {
                                alert(response.data.error.errorstring.cn);
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                }
            }
        },
        /* 查看宏
            1.点击显示主页面切换、还有宏操作记录
            2.再次点击切换（或者隐藏）显示主页面切换、还有宏操作记录
        */
        switchMacros: function(index, Switch, SwitchInfo, name) {
            var value = [];
            if (this.display) {
                if (this.testindex == index) {
                    this.display = false;
                    this.displayMacro1 = !this.displayMacro1;
                    console.log("关闭");
                    this.$emit("nav_switch", "close_nav");
                } else {
                    console.log(JSON.stringify(this.MacrosSwitchInfo));
                    console.log(JSON.stringify(this.bubianMacrosSwitchInfo));
                    if (
                        JSON.stringify(
                            this.MacrosSwitchInfo
                        ) == JSON.stringify(this.bubianMacrosSwitchInfo)
                    ) {
                        console.log(this.testindex);
                        this.$refs.nameInpt.value = name;
                        this.hongname = name;
                        this.testindex = index;
                        this.rightBottom = SwitchInfo; //b
                        this.MacrosSwitchInfo = Switch; //b
                        this.quanju = JSON.parse(JSON.stringify(SwitchInfo));
                        this.quanjuMacrosSwitchInfo = JSON.parse(
                            JSON.stringify(Switch)
                        );
                        this.bubian = JSON.parse(JSON.stringify(SwitchInfo));
                        this.bubianMacrosSwitchInfo = JSON.parse(
                            JSON.stringify(Switch)
                        );
                        this.$store.state.swtichContent = Switch;
                        this.$store.state.swtichInfo = SwitchInfo;
                    } else {
                        this.$confirm("是否要保存当前宏", "提示", {
                            cancelButtonClass: "btn-custom-cancel",
                            confirmButtonText: "确定",
                            cancelButtonText: "取消",
                            type: "warning"
                        })
                            .then(() => {
                                var timestamp = new Date().valueOf();
                                let username = sessionStorage.getItem(
                                    "access_username"
                                );
                                let sessionId = sessionStorage.getItem(
                                    "access_token"
                                );
                                let aoData = {
                                    Authentication: {
                                        UserName: username,
                                        SessionId: sessionId
                                    },
                                    data: [
                                        {
                                            CommandType: "set",
                                            CommandData: {
                                                CommandName: "UpdateMacro",
                                                key: this.key,
                                                macro: {
                                                    name: this.$refs.nameInpt
                                                        .value,
                                                    data: this.MacrosSwitchInfo
                                                }
                                            }
                                        }
                                    ],
                                    Protocol: {
                                        Name: "liguoNetWork",
                                        Version: "1.0.0"
                                    },
                                    JSON: {
                                        Version: "1.0.0"
                                    },
                                    datalen: 1,
                                    request: timestamp,
                                    timestamp: timestamp
                                };
                                let that = this;
                                this.$axios
                                    .post("/cmd", aoData)
                                    .then(function(response) {
                                        if (response.data.status == "success") {
                                            alert("保存成功");
                                        } else if (
                                            response.data.status == "fail"
                                        ) {
                                            alert(
                                                response.data.error.errorstring
                                                    .cn
                                            );
                                        }
                                    })
                                    .catch(function(error) {
                                        console.log(error);
                                    });
                                this.$refs.nameInpt.value = name;
                                this.hongname = name;
                                this.testindex = index;
                                this.rightBottom = SwitchInfo; //b
                                this.MacrosSwitchInfo = Switch; //b
                                this.quanju = JSON.parse(
                                    JSON.stringify(SwitchInfo)
                                );
                                this.quanjuMacrosSwitchInfo = JSON.parse(
                                    JSON.stringify(Switch)
                                );
                                this.bubianMacrosSwitchInfo = JSON.parse(
                                    JSON.stringify(Switch)
                                );
                                this.bubian = JSON.parse(
                                    JSON.stringify(SwitchInfo)
                                );
                                this.$store.state.swtichContent = Switch;
                                this.$store.state.swtichInfo = SwitchInfo;
                            })
                            .catch(() => {
                                console.log(this.testindex);
                                this.$refs.nameInpt.value = name;
                                this.hongname = name;
                                this.testindex = index;
                                this.rightBottom = SwitchInfo; //b
                                this.MacrosSwitchInfo = Switch; //b
                                this.quanju = JSON.parse(
                                    JSON.stringify(SwitchInfo)
                                );
                                this.quanjuMacrosSwitchInfo = JSON.parse(
                                    JSON.stringify(Switch)
                                );
                                this.bubianMacrosSwitchInfo = JSON.parse(
                                    JSON.stringify(Switch)
                                );
                                this.bubian = JSON.parse(
                                    JSON.stringify(SwitchInfo)
                                );
                                this.$store.state.swtichContent = Switch;
                                this.$store.state.swtichInfo = SwitchInfo;
                            });
                    }
                    this.hidden = true;
                    console.log("切换打开");
                }
            } else {
                this.$emit("nav_switch", "open_nav");
                this.display = !this.display;
                this.displayMacro1 = !this.displayMacro1;
                this.$refs.nameInpt.value = name;
                this.hongname = name;
                this.testindex = index;
                this.rightBottom = SwitchInfo; //b
                this.MacrosSwitchInfo = Switch; //b
                this.quanju = JSON.parse(JSON.stringify(SwitchInfo));
                this.quanjuMacrosSwitchInfo = JSON.parse(
                    JSON.stringify(Switch)
                );
                this.bubian = JSON.parse(JSON.stringify(SwitchInfo));
                this.bubianMacrosSwitchInfo = JSON.parse(
                    JSON.stringify(Switch)
                );
                this.$store.state.swtichContent = Switch;
                this.$store.state.swtichInfo = SwitchInfo;
                console.log("打开");
            }
        },
        /* 执行宏 */
        implementMacro: function(index, macroName) {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "set",
                        CommandData: {
                            CommandName: "RunMacro",
                            key: this.key,
                            name: macroName
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        that.display = !that.hidden;
                        that.displayMacro1 = false;
                        that.$emit("nav_switch", "close_nav");
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        /* 删除宏
            1.点击删除提示是否删除
        */
        deleteAtBottom: function(index, macroName) {
            this.$confirm("您确定要删除当前宏吗?", "提示", {
                cancelButtonClass: "btn-custom-cancel",
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    var timestamp = new Date().valueOf();
                    let username = sessionStorage.getItem("access_username");
                    let sessionId = sessionStorage.getItem("access_token");
                    let aoData = {
                        Authentication: {
                            UserName: username,
                            SessionId: sessionId
                        },
                        data: [
                            {
                                CommandType: "set",
                                CommandData: {
                                    CommandName: "DeleteMacro",
                                    key: this.key,
                                    name: macroName
                                }
                            }
                        ],
                        Protocol: {
                            Name: "liguoNetWork",
                            Version: "1.0.0"
                        },
                        JSON: {
                            Version: "1.0.0"
                        },
                        datalen: 1,
                        request: timestamp,
                        timestamp: timestamp
                    };
                    let that = this;
                    this.$axios
                        .post("/cmd", aoData)
                        .then(function(response) {
                            if (response.data.status == "success") {
                                that.display = !that.hidden;
                                that.displayMacro1 = false;
                                that.$emit("nav_switch", "close_nav");
                            } else if (response.data.status == "fail") {
                                alert(response.data.error.errorstring.cn);
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                })
                .catch(() => {});
        },
        /* 右边记录宏的隐藏按钮 */
        closeButton: function() {
            this.display = !this.hidden;
            this.displayMacro1 = false;
            this.$emit("nav_switch", "close_nav");
        },
        /* 右边清空宏记录按钮 */
        emptyingRecord: function(index) {
            // alert("清空宏操作")
            this.$confirm("是否要清空当前宏", "提示", {
                cancelButtonClass: "btn-custom-cancel",
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.rightBottom.splice(index);
                    this.MacrosSwitchInfo.splice(index);
                })
                .catch(() => {});
        },
        reviseBtn: function() {
            if (this.$refs.nameInpt.value == "") {
                this.$alert("名字不可为空", "提示", {
                    confirmButtonText: "确定",
                    callback: action => {}
                });
            } else if (this.$refs.nameInpt.value == this.hongname) {
                var timestamp = new Date().valueOf();
                let username = sessionStorage.getItem("access_username");
                let sessionId = sessionStorage.getItem("access_token");
                let aoData = {
                    Authentication: {
                        UserName: username,
                        SessionId: sessionId
                    },
                    data: [
                        {
                            CommandType: "set",
                            CommandData: {
                                CommandName: "UpdateMacro",
                                key: this.key,
                                macro: {
                                    name: this.$refs.nameInpt.value,
                                    data: this.MacrosSwitchInfo
                                }
                            }
                        }
                    ],
                    Protocol: {
                        Name: "liguoNetWork",
                        Version: "1.0.0"
                    },
                    JSON: {
                        Version: "1.0.0"
                    },
                    datalen: 1,
                    request: timestamp,
                    timestamp: timestamp
                };
                let that = this;
                this.$axios
                    .post("/cmd", aoData)
                    .then(function(response) {
                        if (response.data.status == "success") {
                            alert("保存成功");
                            that.display = !that.hidden;
                            that.displayMacro1 = false;
                            that.$emit("nav_switch", "close_nav");
                        } else if (response.data.status == "fail") {
                            alert(response.data.error.errorstring.cn);
                        }
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else {
                var list_bj = true;
                for (let i = 0; i < this.bottomBoxBlock.length; i++) {
                    if (
                        this.bottomBoxBlock[i].Name ==
                            this.$refs.nameInpt.value &&
                        this.$refs.nameInpt.value != this.hongname
                    ) {
                        this.$alert(
                            "当前名字与其他宏名称重复不可添加",
                            "提示",
                            {
                                confirmButtonText: "确定",
                                callback: action => {}
                            }
                        );
                        list_bj = false;
                        return false;
                    }
                }
                if (list_bj) {
                    // console.log(JSON.parse(JSON.stringify(this.bubian)))
                    // console.log(this.rightBottom)
                    var timestamp = new Date().valueOf();
                    let username = sessionStorage.getItem("access_username");
                    let sessionId = sessionStorage.getItem("access_token");
                    let aoData = {
                        Authentication: {
                            UserName: username,
                            SessionId: sessionId
                        },
                        data: [
                            {
                                CommandType: "set",
                                CommandData: {
                                    CommandName: "AddMacro",
                                    key: this.key,
                                    macro: {
                                        name: this.$refs.nameInpt.value,
                                        data: this.MacrosSwitchInfo
                                    }
                                }
                            }
                        ],
                        Protocol: {
                            Name: "liguoNetWork",
                            Version: "1.0.0"
                        },
                        JSON: {
                            Version: "1.0.0"
                        },
                        datalen: 1,
                        request: timestamp,
                        timestamp: timestamp
                    };
                    let that = this;
                    this.$axios
                        .post("/cmd", aoData)
                        .then(function(response) {
                            if (response.data.status == "success") {
                                alert("保存成功");
                                that.display = !that.hidden;
                                that.displayMacro1 = false;
                                that.$emit("nav_switch", "close_nav");
                            } else if (response.data.status == "fail") {
                                alert(response.data.error.errorstring.cn);
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                }
            }
        },
        /* 右侧删除宏记录 */
        deleteRightBottom: function(index) {
            this.rightBottom.splice(index, 1);
            this.MacrosSwitchInfo.splice(index, 1);
            this.quanju.splice(index, 1);
            this.quanjuMacrosSwitchInfo.splice(index, 1);
        }
    },
    created() {},
    mounted() {}
};
</script>

<style lang="scss" scoped>
@import "../../style/common";
.btn-custom-cancel {
    float: right;
    margin-left: 10px;
}
.clear {
    clear: both;
}
.wrapper {
    min-width: 14px;
    height: 100%;
}
/*左侧 Start*/
.wrapper_left {
    width: 200px;
    height: 100%;
    background: #fff;
    /*display: inline-block;*/
    float: left;
    position: relative;
    margin-left: 14px;
}
/*隐藏按钮*/
.hiddenDutton {
    width: 14px;
    height: 28px;
    position: absolute;
    left: -14px;
    top: 50%;
    margin-top: -14px;
    cursor: pointer;
    background: url(../../../static/img/sprite.png) no-repeat -106px -30px;
}
/*显示按钮*/
.classDisplayMacro {
    width: 14px;
    height: 28px;
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -14px;
    cursor: pointer;
    background: url(../../../static/img/sprite.png) no-repeat -90px -30px;
    z-index: 100;
    /*background: red;*/
}
.wrapper_left_plot {
    text-align: center;
    font-size: 14px;
    line-height: 28px;
    margin-bottom: 10px;
    margin-top: 10px;
}
.wrapper_left_close {
    width: 28px;
    height: 28px;
    margin-top: 10px;
    float: right;
    cursor: pointer;
    background: url(../../../static/img/sprite.png) no-repeat 0px -210px;
}
.wrapper_left_close:hover {
    background: url(../../../static/img/sprite.png) no-repeat -29px -210px;
}
.wrapper_left_preservation {
    clear: both;
    text-align: center;
    font-size: 14px;
    margin-bottom: 10px;
}
.wrapper_left_keepName {
    font-size: 12px;
    padding: 0 5px;
    width: 140px;
    height: 25px;
    margin-bottom: 10px;
    float: left;
    outline: none;
}
/*按钮图标公用样式*/
.wrapper_left_button {
    width: 28px;
    height: 28px;
    margin-left: 5px;
    border-radius: 50%;
    cursor: pointer;
    border: none;
    outline: none;
}
.wrapper_left_preservationBton {
    float: left;
    background: url(../../../static/img/sprite.png) no-repeat 0px -60px;
}
.wrapper_left_nopreservationBton {
    cursor: no-drop;
    float: left;
    background: url(../../../static/img/sprite.png) no-repeat 0px -270px;
}
.wrapper_left_preservationBton:hover {
    background: url(../../../static/img/sprite.png) no-repeat -30px -60px;
}
.wrapper_left_searchMacro {
    height: 45px;
    border-top: 1px solid #000;
}
.wrapper_left_searchMacro_inpputMacro {
    font-size: 12px;
    padding: 0 5px;
    width: 175px;
    height: 25px;
    margin-top: 10px;
    outline: none;
}
.wrapper_left_topBox {
    width: 190px;
    background: #fff;
    padding: 0 5px;
}
.wrapper_left_bottomBox {
    width: 100%;
    /*height: 100%;*/
    height: calc(100% - 180px);
    /*padding-right: 20px;*/
    overflow: auto;
}
/*滚动条整体样式*/
/* .wrapper_left_bottomBox::-webkit-scrollbar {
            width: 4px;
            height: 4px;
        }*/
/*定义滑块 内阴影+圆角*/
/*.wrapper_left_bottomBox::-webkit-scrollbar-thumb {
            border-radius: 2px;
            background-color: rgba(0, 0, 0, 0.2);
        }*/
/*定义滚动条轨道 内阴影+圆角*/
/*.wrapper_left_bottomBox::-webkit-scrollbar-track {
            border-radius: 2px;
            background-color: rgba(0, 0, 0, 0.1);
        }*/
.wrapper_left_bottomBox_block {
    width: 173px;
    height: 100px;
    padding: 5px;
    border-bottom: 1px solid #797979;
    background: #fff;
    position: relative;
}
.wrapper_left_bottomBox_block_ico_box {
    position: absolute;
    right: 0;
    bottom: 10px;
}
.wrapper_left_bottomBox_block_ico {
    width: 22px;
    height: 11px;
    background: url(../../../static/img/sprite.png) no-repeat -90px 0px;
    margin-top: 10px;
}
.wrapper_left_bottomBox_block_btn {
    width: 28px;
    height: 28px;
    background: pink;
    float: left;
    margin-right: 10px;
    cursor: pointer;
    border-radius: 50%;
}
.block_btn1 {
    background: url(../../../static/img/sprite.png) no-repeat 0px 0px;
}
.block_btn1:hover {
    background: url(../../../static/img/sprite.png) no-repeat -30px 0px;
}
.block_btn2 {
    background: url(../../../static/img/sprite.png) no-repeat 0px -30px;
}
.block_btn2:hover {
    background: url(../../../static/img/sprite.png) no-repeat -30px -30px;
}
.block_btn3 {
    background: url(../../../static/img/sprite.png) no-repeat 0px -90px;
}
.block_btn3:hover {
    background: url(../../../static/img/sprite.png) no-repeat -30px -90px;
}
/*左侧 End*/

/*右侧 Start*/
.wrapper_right {
    /*min-width:200px;*/
    width: 220px;
    height: 100%;
    padding: 5px;
    margin-left: 10px;
    background: #fff;
    box-sizing: border-box;
    float: left;
    /*display: inline-block;*/
}
.wrapper_right_head {
    width: 100%;
    height: 80px;
}
.wrapper_right_head_btn {
    width: 28px;
    height: 28px;
    float: left;
    margin-top: 5px;
    margin-right: 10px;
    cursor: pointer;
    border-radius: 50%;
}
.right_head_btn_close {
    background: url(../../../static/img/sprite.png) no-repeat 0px -210px;
}
.right_head_btn_close:hover {
    background: url(../../../static/img/sprite.png) no-repeat -29px -210px;
}
.right_head_btn_empty {
    background: url(../../../static/img/sprite.png) no-repeat 0px -240px;
}
.right_head_btn_empty:hover {
    background: url(../../../static/img/sprite.png) no-repeat -29px -240px;
}
.wrapper_right_head_input {
    font-size: 12px;
    padding: 0 5px;
    width: 140px;
    height: 25px;
    margin-top: 10px;
    float: left;
    outline: none;
}
.wrapper_right_head_keepBtn {
    margin-top: 10px;
    background: url(../../../static/img/sprite.png) no-repeat 0px -60px;
}
.wrapper_right_head_keepBtn:hover {
    background: url(../../../static/img/sprite.png) no-repeat -30px -60px;
}
.wrapper_right_bottom {
    margin-top: 10px;
    /*height: 100%;*/
    height: calc(100% - 85px);
    overflow: auto;
}
/*滚动条整体样式*/
/* .wrapper_right_bottom::-webkit-scrollbar {
            width: 4px;
            height: 4px;
        }*/
/*定义滑块 内阴影+圆角*/
/* .wrapper_right_bottom::-webkit-scrollbar-thumb {
            border-radius: 2px;
            background-color: rgba(0, 0, 0, 0.2);
        }*/
/*定义滚动条轨道 内阴影+圆角*/
/*.wrapper_right_bottom::-webkit-scrollbar-track {
            border-radius: 2px;
            background-color: rgba(0, 0, 0, 0.1);
        }*/
.wrapper_right_bottom_box {
    height: 30px;
    margin-bottom: 10px;
    font-size: 12px;
    color: #000;
    /*font-size: 0;*/
}
.wrapper_right_bottom_box_number {
    width: 10px;
    float: left;
    /*display: inline-block;*/
    margin-right: 10px;
}
.wrapper_right_bottom_box_right {
    width: 140px;
    float: left;
    /*display: inline-block;*/
}
.wrapper_right_bottom_box_name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.wrapper_right_bottom_box_record {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.wrapper_right_bottom_box_delete {
    width: 11px;
    height: 15px;
    float: left;
    margin-top: 10px;
    margin-left: 5px;
    cursor: pointer;
    background: url(../../../static/img/sprite.png) no-repeat -90px -13px;
}
.wrapper_right_bottom_box_delete:hover {
    background: url(../../../static/img/sprite.png) no-repeat -103px -13px;
}

/* 右侧宏操作动画 */
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
    transition: all 0.3s ease;
}
.slide-fade-leave-active {
    transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
}
/*右侧 End*/
</style>


