<template>
    <div class="wrapper">
        <div class="list_logo">
            <div class="list_logo_img" @click="setUp"></div>
        </div>
        <div>
            <el-collapse v-model="activeNames" id="device_list">
                <el-collapse-item title="Pro AV" name="1" class="list_title">
                    <div @click="selected(index)" class="proListInfo" :class="{selected:index==nowIndex}" v-for="(items,index) in proAVlist" :key="index">{{items.Name}}<br/>{{items.ModuleName}}</div>
                </el-collapse-item>
                <el-collapse-item title="AV Streaming" name="2" class="list_title">
                    <div @click="selected1(index,items.Key)" class="proListInfo" :class="{selected:index==nowIndex1}" v-for="(items,index) in streamingList" :key="index">{{items.Name}}<br/>{{items.ModuleName}}</div>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
import bus from "../../utils/eventBus.js";
export default {
    components: {},
    props: {},
    data() {
        return {
            activeNames: ["1"],
            nowIndex: 0,
            nowIndex1: 0,
            proAVlist: [],
            streamingList: []
        };
    },
    watch: {},
    computed: {},
    methods: {
        selected(index) {
            if (window.myInterval1) {
                window.clearInterval(window.myInterval1);
            }

            let key = this.proAVlist[index].Key;
            let moduleName = this.proAVlist[index].ModuleName;
            this.$store.state.key = key;
            this.$store.state.moduleName = moduleName;
            this.nowIndex = index;
            this.getProInfo(key, moduleName);
            let that = this;
            window.myInterval1 = setInterval(function() {
                that.getProInfo(key, moduleName);
            }, 2000);
        },
        selected1(index) {
            this.nowIndex1 = index;
        },
        getProInfo(key, moduleName) {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "get",
                        CommandData: {
                            CommandName: "DeviceInfo",
                            key: key,
                            ModuleName: moduleName
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    that.$store.state.proAvInfo = response.data.data;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },

        setUp: function() {
            this.$emit("setting", "settingInfo");
        },
        getList() {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "get",
                        CommandData: {
                            CommandName: "Devicelist"
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        let length = response.data.data[0].length;
                        for (let i = 0; i < length; i++) {
                            if (response.data.data[0][i]._id == "Pro AV") {
                                that.proAVlist = response.data.data[0][i].list;
                            } else if (
                                response.data.data[0][i]._id == "AV Streaming"
                            ) {
                                that.streamingList =
                                    response.data.data[0][i].list;
                            }
                        }
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    },
    created() {
        var timestamp = new Date().valueOf();
        let username = sessionStorage.getItem("access_username");
        let sessionId = sessionStorage.getItem("access_token");
        let aoData = {
            Authentication: {
                UserName: username,
                SessionId: sessionId
            },
            data: [
                {
                    CommandType: "get",
                    CommandData: {
                        CommandName: "Devicelist"
                    }
                }
            ],
            Protocol: {
                Name: "liguoNetWork",
                Version: "1.0.0"
            },
            JSON: {
                Version: "1.0.0"
            },
            datalen: 1,
            request: timestamp,
            timestamp: timestamp
        };
        let that = this;
        this.$axios
            .post("/cmd", aoData)
            .then(function(response) {
                if (response.data.status == "success") {
                    let length = response.data.data[0].length;
                    for (let i = 0; i < length; i++) {
                        if (response.data.data[0][i]._id == "Pro AV") {
                            that.proAVlist = response.data.data[0][i].list;
                        } else if (
                            response.data.data[0][i]._id == "AV Streaming"
                        ) {
                            that.streamingList = response.data.data[0][i].list;
                        }
                    }
                    that.selected(0);
                } else if (response.data.status == "fail") {
                    alert(response.data.error.errorstring.cn);
                }
            })
            .catch(function(error) {
                console.log(error);
            });
    },
    mounted() {
        let that = this;
        window.myInterval = setInterval(function() {
            that.getList();
        }, 2000);
    }
};
</script>
<style>
.el-collapse-item__header {
    font-size: 16px !important;
}
.el-collapse-item__content {
    padding-bottom: 0;
    background: #4d4d4d;
    color: white;
}
</style>

<style lang="scss" scoped>
@import "../../style/common";
.wrapper {
    width: 200px;
}
.list_logo {
    width: 100%;
    margin-top: 5px;
    text-align: center;
}
.list_logo_img {
    width: 28px;
    height: 28px;
    margin: 0 auto;
    border-radius: 50%;
    cursor: pointer;
    background: url(../../../static/img/sprite.png) no-repeat 0px -300px;
}
.list_logo_img:hover {
    background: url(../../../static/img/sprite.png) no-repeat -30px -300px;
}
#device_list {
    background: white;
    box-shadow: 2px 2px 2px #ccc;
    margin-top: 10px;
}
.list_title {
    padding-left: 5px;
    padding-right: 5px;
}
.proListInfo {
    text-align: center;
    cursor: pointer;
    border-bottom: 1px solid white;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.selected {
    border-left: 5px solid #3080b5;
    background: #ededed;
    color: #000000;
}
@media (min-width: 1025px) {
    .proListInfo:hover {
        text-decoration: none;
        background: linear-gradient(
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 0.5)
        ); /* 标准的语法（必须放在最后） */
        /*background-color: #444;*/
        color: #ffffff;
    }

    .selected:hover {
        /*text-decoration: none;*/
        background-color: #ccc;
        color: black;
    }
}
</style>


